/* make marks readable and consistently styled */
.editor-content mark {
  padding: 0 0.08em;
  border-radius: 2px;
  color: inherit; /* keep normal text color */
}

/* force the same colors as the legend, even with prose defaults */
.editor-content mark[data-highlight-color="yellow"] {
  background-color: #fefcbf !important; /* pastel yellow */
  border: 1px solid #fde68a !important;
}

.editor-content mark[data-highlight-color="red"] {
  background-color: #fecaca !important; /* pastel red */
  border: 1px solid #fca5a5 !important;
}

.editor-content mark[data-highlight-color="blue"] {
  background-color: #dbeafe !important; /* lighter blue for readability */
  border: 1px solid #93c5fd !important;
}

/* legend squares, keep in sync with the marks */
.legend-yellow { background-color: #fefcbf; border: 1px solid #fde68a; }
.legend-red    { background-color: #fecaca; border: 1px solid #fca5a5; }
.legend-blue   { background-color: #dbeafe; border: 1px solid #93c5fd; }

/* headings and lists */
.editor-content h1 { font-size: 1.5em; margin: .75em 0 .25em; }
.editor-content ul { list-style-type: disc; margin-left: 1.5em; }
.editor-content ol { list-style-type: decimal; margin-left: 1.5em; }
